


import React from 'react';
import Grid from '@material-ui/core/Grid';
import io from "socket.io-client";

import LeftPane from './LeftPane';
import RightPane from './RightPane';
import ChatInput from './ChatInput';



export default class Chat extends React.Component{

    constructor(){
        super();
        this.state = {
            msg:{
                sender:'vivek',
                data:'',
                log:null,
                type:0
            },
            chat:[]
        };
        this.socket = null;

        this.emptyMsg = {
            sender:'vivek',
            data:'',
            log:null,
            type:0
        }

        this.setMsg = this.setMsg.bind(this);
        this.handleEnterPress = this.handleEnterPress.bind(this);
    }

    componentWillMount(){
        this.socket = io('http://localhost:3002');
        this.socket.emit('chat message', "Message from Chat App");
    }

    componentDidMount(){
        this.socket.on('broadcast',(data)=>{
            console.log('componentDidMount-->',data);
            var tempChat =  this.state.chat.slice();;
            tempChat.unshift(data);
            this.setState({
                chat:tempChat
            },()=>{
                console.log(this.state)
            })
        })
    }

    setMsg(event){
        var tempMsg = Object.assign({},this.state.msg);
        tempMsg['data'] = event.target.value;
        tempMsg['log'] = (new Date()).toLocaleString();
        this.setState({
            msg:tempMsg
        },()=>{
            console.log(this.state)
        })
    }

    handleEnterPress(event){
        if(event.key === 'Enter' && this.state.msg.data){
            var tempChat =  this.state.chat.slice();;
            tempChat.unshift(this.state.msg);
            var socketData = this.state.msg;
            this.setState({
                chat:tempChat,msg:Object.assign({},this.emptyMsg)
            },()=>{
                console.log(this.state)
            })
            console.log(socketData)
            this.socket.emit('chatMsg', socketData);
        }
    }
    
    render(){
        return( 
            <Grid container>
                <Grid item sm={9} xs={12}>
                    <LeftPane chats={this.state.chat} />
                </Grid>
                {/* <Grid item  sm={3} xs={12}>
                    <RightPane />
                </Grid> */}
                <Grid item sm={12} xs={12}>
                    <ChatInput msg={this.state.msg.data} setMsg={this.setMsg} handleEnterPress={this.handleEnterPress} />
                </Grid>
            </Grid>
        )
    }
    
}